img {
  max-width: 100%;
  height: auto;
}

.line-break {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }

  &--desktop {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
    }
  }
}

@keyframes slide {
  from {
    transform: translateY(-100%);
  }

  to {
    transform: translateY(0);
  }
}
