@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}

.line-break {
  display: none;
}
@media (min-width: 768px) {
  .line-break {
    display: block;
  }
}
.line-break--desktop {
  display: none;
}
@media (min-width: 1200px) {
  .line-break--desktop {
    display: block;
  }
}

@keyframes slide {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.page {
  font-family: "PT sans", "Arial", sans-serif;
  height: 100%;
  font-size: 18px;
  line-height: 26px;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ffffff;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
}
.page__body--shadow {
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  position: relative;
}
@media (max-width: 767px) {
  .main-header {
    min-height: 56px;
  }
}

.main-header__logo {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

.main-header__nav-toggle {
  display: block;
  width: 23px;
  height: 22px;
  position: absolute;
  z-index: 5;
  border: 0;
  background-image: url("../img/icons/menu-icon_close.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  cursor: pointer;
  right: 20px;
  top: 17px;
  transition: 0.3s ease-in;
}
.main-header__nav-toggle:hover {
  opacity: 0.6;
}
.main-header__nav-toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-header__nav-toggle {
    display: none;
  }
}

.main-header__nav-toggle--open {
  background-image: url("../img/icons/menu-icon_open.svg");
}

.main-nav {
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
  }
}
@media (max-width: 767px) {
  .main-nav {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    right: 0;
    z-index: 4;
  }
  .main-nav--close {
    display: none;
  }
  .main-nav--open {
    animation-name: slide;
    animation-duration: 1s;
    animation-timing-function: cubic-bezier(0.4, -0.31, 0.68, 1.34);
    animation-iteration-count: 1;
    animation-direction: alternate;
    animation-fill-mode: forwards;
    display: block;
    box-shadow: 0 -5px 0 5px #81b3d2;
  }
  .main-nav--no-js {
    display: block;
  }
}

.main-nav__list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}

@media (max-width: 767px) {
  .main-nav__item {
    box-shadow: 0 1px 0 0 rgba(0, 1, 1, 0.2), 0 2px 0 0 rgba(255, 255, 255, 0.2) inset;
  }
  .main-nav__item:first-child {
    box-shadow: 0 1px 0 0 rgba(0, 1, 1, 0.2);
  }
  .main-nav__item:last-child {
    box-shadow: 0 2px 0 0 rgba(255, 255, 255, 0.2) inset;
    position: relative;
  }
  .main-nav__item:last-child::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 7px;
    bottom: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    z-index: 3;
  }
  .main-nav__item:hover {
    background-color: #669ec0;
  }
  .main-nav__item:active {
    background-color: #5496bd;
  }
  .main-nav__item:active a {
    color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav__list a {
  display: block;
  padding: 17px 20px 12px;
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  transition: 0.3s linear;
}
@media (min-width: 768px) {
  .main-nav__list a {
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    padding-top: 18px;
    padding-bottom: 17px;
  }
  .main-nav__list a:hover {
    color: #81b3d2;
  }
  .main-nav__list a:active {
    color: rgba(0, 0, 0, 0.3);
  }
}

@media (min-width: 768px) {
  .main-nav__item--current a {
    color: #766357;
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 768px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    width: 1200px;
  }
}

@media (min-width: 768px) {
  .main-nav__item:nth-child(4n+4) {
    grid-column: 5/6;
  }

  .main-nav__item:nth-child(4n+3) {
    grid-column: 4/5;
  }
}
